
#gameScreen {
  margin: 0 auto;
}
#gameScreen .top-bar {
  margin: 0 auto;
  padding: 14px;
  width: 900px;
  height: 126px;
  background: url("/static/img/top-bar-background.svg")
}
#gameScreen .top-bar .button {
  background: url("/static/img/button.svg");
  width: 234px;
  height: 48px;
  border: 0;
  border-radius: 2px;
  font-size: 20px;
  color: rgba(255,255,255,0.8);
}
#gameScreen .top-bar button:disabled {
  color: #666;
}
#gameScreen .top-bar .battle-button {
  background: url("/static/img/battle-button.svg");
  width: 234px;
  height: 48px;
  border: 0;
  border-radius: 2px;
  margin-bottom: 6px;
  font-size: 20px;
  color: rgba(255,255,255,0.8);
}
#gameScreen .top-bar button:focus {
  outline: none;
}
#gameScreen .input-container {
  width: 100%;
}
#gameScreen .input-container label {
  color: rgba(0,0,0,.54);
  display: block;
  font-weight: bold;
  font-size: 16px;
}
#gameScreen .input-container input[type='text'] {
  background: transparent;
  border: none;
  border-bottom: 2px solid rgba(0,0,0,0.34);
  width: 100%;
  height: 32px;
  color: rgba(0,0,0,.54);
  line-height: 32px;
  font-size: 20px;
}
#gameScreen .input-container input[type='text']:focus {
  outline: none;
}
#gameScreen #phaser {
  margin: 10px auto 0;
  padding: 6px;
  width: 900px;
  height: 668px;
  background: url("/static/img/game-container-background.svg");
}
.large-button {
  width: 90% !Important;
}

